
*{
  margin: 0;
  padding: 0;
}
html, body {
  height: 100%;
}
body {
  overflow: hidden;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  width: 100%;
  height: 100%;
}
div.background {
  position: absolute;
  width: 100%;
  height: 100%;
}
nav {
  position: absolute;
  width: 100%;
  height: 300px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background-color: rgba(0, 0, 0, 0.5);
}
ul {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  margin: auto;
}
nav li {
  list-style: none;
  max-width: 250px;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
nav a {
  display: block;
  color: white;
  text-decoration: none;
}
nav .active {
  background: black;
}




